.category {
	&__title {
		margin: 0;
		margin-bottom: 24px;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center
	}

	&__item {
		margin-right: 15px;
		margin-bottom: 30px;

	}

	&__image {
		object-fit: cover;
		padding: 10% 0px 0 30%;
		width: 350px;
		height: 430px;
		object-position: left;

		&-block {
			height: 370px;
			width: 370px;
			overflow: hidden;
			margin-bottom: 24px;
		}
	}

	&__models-number {
		color: #89939A;
	}
}

.category__item:last-child{
	margin-right: 0;
}